<template>
  <div class="study-group p-4">
    <StudyGroupDetail :studyGroup="studyGroup" />
  </div>
</template>

<script>
import StudyGroupDetail from '../components/StudyGroupDetail.vue';

export default {
  name: 'StudyGroup',
  components: {
    StudyGroupDetail,
  },
  data() {
    return {
      studyGroup: null,
    };
  },
  created() {
    const groupId = this.$route.params.id;
    this.studyGroup = this.$store.state.studyGroups.find(
      (group) => group.id == groupId
    );
  },
};
</script>

<style scoped>
.study-group {
  padding: 2em;
}
</style>

