import { createRouter, createWebHistory } from 'vue-router';
import HomeView from '../views/HomeView.vue';
import StudyGroups from '../views/StudyGroups.vue';
import StudyGroup from '../views/StudyGroup.vue';
import LoginView from '../views/LoginView.vue';
import RegisterView from '../views/RegisterView.vue';

const routes = [
  { path: '/', name: 'Home', component: HomeView },
  { path: '/study-groups', name: 'StudyGroups', component: StudyGroups },
  { path: '/study-group/:id', name: 'StudyGroup', component: StudyGroup },
  { path: '/login', name: 'Login', component: LoginView },
  { path: '/register', name: 'Register', component: RegisterView },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;
