<template>
  <div class="register">
    <UserAuth mode="register" @auth="handleAuth" />
  </div>
</template>

<script>
import UserAuth from '../components/UserAuth.vue';

export default {
  name: 'RegisterView',
  components: {
    UserAuth,
  },
  methods: {
    handleAuth(user) {
      this.$store.dispatch('login', user);
      this.$router.push('/');
    },
  },
};
</script>

<style scoped>
.register {
  padding: 2em;
}
</style>
