<template>
  <div class="resource-share">
    <h3>Shared Resources</h3>
    <ul>
      <li v-for="resource in resources" :key="resource.id">
        <a :href="resource.url" target="_blank">{{ resource.name }}</a>
      </li>
    </ul>
    <form @submit.prevent="addResource">
      <div>
        <label for="name">Resource Name:</label>
        <input type="text" id="name" v-model="newResource.name" required />
      </div>
      <div>
        <label for="url">Resource URL:</label>
        <input type="url" id="url" v-model="newResource.url" required />
      </div>
      <button type="submit">Add Resource</button>
    </form>
  </div>
</template>

<script>
export default {
  name: 'ResourceShare',
  props: {
    resources: Array,
  },
  data() {
    return {
      newResource: {
        name: '',
        url: '',
      },
    };
  },
  methods: {
    addResource() {
      this.$emit('add-resource', this.newResource);
      this.newResource.name = '';
      this.newResource.url = '';
    },
  },
};
</script>

<style scoped>
.resource-share {
  padding: 2em;
}
</style>
