import { createStore } from 'vuex';
import api from '../services/api';

export default createStore({
  state: {
    user: null,
    studyGroups: [],
  },
  mutations: {
    setUser(state, user) {
      state.user = user;
    },
    setStudyGroups(state, studyGroups) {
      state.studyGroups = studyGroups;
    },
  },
  actions: {
    async fetchStudyGroups({ commit }) {
      try {
        const response = await api.getStudyGroups();
        commit('setStudyGroups', response.data);
      } catch (error) {
        console.error('Error fetching study groups:', error);
      }
    },
    async login({ commit }, user) {
      try {
        const response = await api.loginUser(user);
        commit('setUser', response.data);
      } catch (error) {
        console.error('Error logging in:', error);
      }
    },
    async register({ commit }, user) {
      try {
        const response = await api.registerUser(user);
        commit('setUser', response.data);
      } catch (error) {
        console.error('Error registering:', error);
      }
    },
  },
  modules: {},
});
